.App {
  height: 90vmin;
  width: 80vmin;
  margin: 0%;
  padding: 0%;
  text-align: center;
}
.node{
 height: 1.5vw;
 width: 3vw;
 border: 1px solid blue;
 align-items: center;
 margin-left: 0px;
 padding: 0px;
}
.nodeStart{
 background-color: green;
}
.nodeFinish{
  background-color: red;
}
.nodeFinish2{
  background-color: chocolate;
}
.nodeVisited{
  background-color: rgb(0, 0, 117);
}
.nodeVisited2{
  background-color: rgb(108, 0, 151);
}
.nodePath{
  background-color: yellow;
}
.wall{
  background-color: black;
}
